<script>
    
    import {
         mapGetters
     } from 'vuex'
    import axios from 'axios'
    import { API } from '../store/config.js'
export default {
    data(){
        return{
            itemm:[],
            sum:0,
            mony:0,
            count:0,
            deletee:'',
            IsDishes:'',
            CountID:[],
            order:[],
            orderone:[],
            loading:false

        }
    },
    mounted() {
        this.sum=this.mony*this.$store.state.foodslist.length
        for(var i=0;i<this.$store.state.foodslist.length;i++){
          this.order.push({            
                "id":this.$store.state.foodslist[i].food.id,
                "amount":this.$store.state.foodslist[i].count
             }
            )
            this.sum=this.sum+this.$store.state.foodslist[i].food.price
        }
     
            

    },
    methods: {
        minus(foodslist,index,count)
        {
            console.log(count);
            if(foodslist[index].count>1){
            foodslist[index].count--
            this.order[index].amount--
            if(this.sum>0){
            this.sum=this.sum-foodslist[index].food.price
            }
         
            }
        },
        pilus(foodslist,index,count)
        { console.log(count);
            foodslist[index].count++,
            this.order[index].amount++
            this.sum=this.sum+foodslist[index].food.price
            console.log(this.order);
        },
        orderpost(){
            
            const token = localStorage.getItem('D-Token')
           axios
            .post (API+'order/create',this.order,
            {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }
                }
                )
                  .then(res=>{
                    console.log(res.data.data);
                    this.orderone=res.data.data,
                    this.$store.dispatch("check",this.orderone)
                    this.$toasted.show("The order was accepted" , {
                    duration: 2000,
                })
                this.loading=false                
                  })
        }
        
    },
    computed: {
             ...mapGetters(["data","foodslist"])
         },
}
</script>
<style>
    body{
        overflow-x: hidden;
